.homepage {
  border: rgb(235, 238, 240) 1px solid;
  height: 100%;
  width: 600px;
}

.homepage-header {
  padding: 16px 15px;
  border-bottom: rgb(235, 238, 240) 1px solid;
  position: sticky;
  top: 0px;
  background-color: white;
  z-index: 1;
}

// Separator
.separator {
  height: 10px;
  border-bottom: rgb(235, 238, 240) 1px solid;
  background-color: rgb(247, 249, 250);
}
